import React from "react"
import {
  Container,
  Grid,
  Image,
  Header,
  Button,
  Segment,
} from "semantic-ui-react"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import querystring from "query-string"
import { labels } from "../../selections"
import Layout from "../layout"
import "./override.css"

const Selection = ({ selection, description, options, previewSrc, result }) => (
  <Layout>
    <Container fluid>
      <Grid padded stackable>
        <Grid.Column width={5} style={{ paddingRight: "5px" }}>
          <Segment style={{ border: 0, boxShadow: "none" }}>
            <Header style={{ color: "#525C73" }} as="h1">
              Your selection: {labels[selection]}
            </Header>
            <p>{description}</p>
          </Segment>
          {options}
        </Grid.Column>
        <Grid.Column width={11} style={{ paddingLeft: 0 }}>
          <Grid>
            <Grid.Row columns="equal">
              <Grid.Column>
                <AniLink swipe direction="right" to="/steps/1">
                  <Button
                    fluid
                    style={{
                      backgroundColor: "#D4D4D4",
                      borderRadius: 0,
                      color: "#3F4B65",
                    }}
                  >
                    Back to Selection
                  </Button>
                </AniLink>
              </Grid.Column>
              <Grid.Column>
                <AniLink
                  swipe
                  direction="left"
                  to={`/steps/3?${querystring.stringify(result)}`}
                >
                  <Button
                    fluid
                    style={{
                      backgroundColor: "#50816F",
                      borderRadius: 0,
                      color: "white",
                    }}
                  >
                    Submit Design for Pricing and Consultation
                  </Button>
                </AniLink>
              </Grid.Column>
            </Grid.Row>
            <Grid.Row>
              <Grid.Column>
                <Image
                  src={previewSrc}
                  style={{ border: "solid 8px #D4D4D4" }}
                />
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Grid.Column>
      </Grid>
    </Container>
  </Layout>
)

export default Selection
