import React from "react"
import { Segment } from "semantic-ui-react"

const OptionsColor = ({ value, label, selected, onClick }) => (
  <Segment
    onClick={onClick}
    style={{
      padding: 0,
      paddingBottom: "100px",
      border: selected ? "solid 4px red" : 0,
      minHeight: "70px",
      maxHeight: "70px",
      maxWidth: "160px",
      borderRadius: 0,
      boxShadow: "none",
    }}
  >
    <div
      style={{
        padding: 0,
        minHeight: "70px",
        maxHeight: "70px",
        maxWidth: "160px",
        backgroundColor: value,
      }}
    />
    <span>{label}</span>
  </Segment>
)
export default OptionsColor
