import React from "react"
import { Grid } from "semantic-ui-react"
import { useMediaQuery } from "react-responsive"
import chunk from "lodash/chunk"
import breakpoints from "../../../breakpoints"
import OptionsColor from "./OptionsColor"

const ResponsiveColors = props => {
  const isDesktop = useMediaQuery({
    query: `(min-width: ${breakpoints.desktop.min}px)`,
  })
  return isDesktop ? (
    <Colors numberOfChunks={3} {...props} />
  ) : (
    <Colors numberOfChunks={2} {...props} />
  )
}

const Colors = ({ numberOfChunks, color, colors, onSelect }) => (
  <React.Fragment>
    {chunk(colors, numberOfChunks).map(colorsChunk => (
      <Grid.Row columns="equal" style={{ paddingTop: "6px" }}>
        {colorsChunk.map(({ id, value, label }) => (
          <Grid.Column>
            <OptionsColor
              value={value}
              label={label}
              selected={color === id}
              onClick={() => onSelect(id)}
            />
          </Grid.Column>
        ))}
      </Grid.Row>
    ))}
  </React.Fragment>
)

export default ResponsiveColors
