import React from "react"
import { Grid } from "semantic-ui-react"
import OptionsHeader from "../garageOptions/OptionsHeader"
import MoreOptions from "../garageOptions/MoreOptions"
import Colors from "../garageOptions/Colors"
import Doors from "./Doors"

const colors = [
  { id: 1, value: "#000001", label: "Color #1" },
  { id: 2, value: "#000002", label: "Color #2" },
  { id: 3, value: "#000003", label: "Color #3" },
  { id: 4, value: "#000004", label: "Color #4" },
  { id: 5, value: "#000005", label: "Color #5" },
  { id: 6, value: "#000006", label: "Color #6" },
]

const GarageOptions = ({ color, door, onSelect }) => (
  <Grid padded>
    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <OptionsHeader label="Select Your Door Style" />
      </Grid.Column>
    </Grid.Row>

    <Doors door={door} onSelect={door => onSelect({ color, door })} />

    <Colors
      color={color}
      colors={colors}
      onSelect={colorId => onSelect({ color: colorId, door })}
    />

    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <MoreOptions />
      </Grid.Column>
    </Grid.Row>
  </Grid>
)

export default GarageOptions
