import React from "react"
import { Grid, Segment, Image } from "semantic-ui-react"
import { useMediaQuery } from "react-responsive"
import chunk from "lodash/chunk"
import breakpoints from "../../../breakpoints"
import door from "./door.png"

const doors = [
  {
    id: 1,
    src: door,
    label: "Style #1",
  },
  {
    id: 2,
    src: door,
    label: "Style #2",
  },
  {
    id: 3,
    src: door,
    label: "Style #3",
  },
  {
    id: 4,
    src: door,
    label: "Style #4",
  },
  {
    id: 5,
    src: door,
    label: "Style #5",
  },
  {
    id: 6,
    src: door,
    label: "Style #6",
  },
]

const ResponsiveDoors = props => {
  const isDesktop = useMediaQuery({
    query: `(min-width: ${breakpoints.desktop.min}px)`,
  })
  return isDesktop ? (
    <Doors numberOfChunks={6} {...props} />
  ) : (
    <Doors numberOfChunks={3} {...props} />
  )
}

const Doors = ({ numberOfChunks, door, onSelect }) => (
  <React.Fragment>
    {chunk(doors, numberOfChunks).map(doorsChunk => (
      <Grid.Row columns="equal">
        {doorsChunk.map(({ id, src, label }) => (
          <Grid.Column>
            <Segment
              color={door === id ? "red" : null}
              onClick={() => onSelect(id)}
              style={{ padding: 0 }}
            >
              <Image src={src} />
              <span>{label}</span>
            </Segment>
          </Grid.Column>
        ))}
      </Grid.Row>
    ))}
  </React.Fragment>
)

export default ResponsiveDoors
