import React from "react"
import { Button } from "semantic-ui-react"

const OptionsButton = ({ label, selected, onClick }) => (
  <Button
    fluid
    onClick={onClick}
    style={{
      backgroundColor: selected ? "red" : "#50816F",
      borderRadius: 0,
      color: "white",
    }}
  >
    {label}
  </Button>
)
export default OptionsButton
