import React from "react"
import { Grid } from "semantic-ui-react"
import OptionsHeader from "./OptionsHeader"
import OptionsButton from "./OptionsButton"
import MoreOptions from "./MoreOptions"
import Colors from "./Colors"

const colors = [
  { id: 1, value: "#000001", label: "Color #1" },
  { id: 2, value: "#000001", label: "Color #2" },
  { id: 3, value: "#000001", label: "Color #3" },
  { id: 4, value: "#000001", label: "Color #4" },
  { id: 5, value: "#000001", label: "Color #5" },
  { id: 6, value: "#000001", label: "Color #6" },
]

const flooringColors = [
  { id: 1, value: "#000001", label: "Floor #1" },
  { id: 2, value: "#000001", label: "Floor #2" },
  { id: 3, value: "#000001", label: "Floor #3" },
  { id: 4, value: "#000001", label: "Floor #4" },
  { id: 5, value: "#000001", label: "Floor #5" },
  { id: 6, value: "#000001", label: "Floor #6" },
]

const GarageOptions = ({ theme, color, floor, onSelect }) => (
  <Grid padded>
    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <OptionsHeader label="Select Your Theme Garage" />
      </Grid.Column>
    </Grid.Row>
    <Grid.Row columns="equal" style={{ paddingTop: "6px" }}>
      <Grid.Column>
        <OptionsButton
          label="Theme #1"
          selected={theme === 1}
          onClick={() => onSelect({ theme: 1, color, floor })}
        />
      </Grid.Column>
      <Grid.Column>
        <OptionsButton
          label="Theme #2"
          selected={theme === 2}
          onClick={() => onSelect({ theme: 2, color, floor })}
        />
      </Grid.Column>
    </Grid.Row>
    <Grid.Row columns="equal" style={{ paddingTop: "6px" }}>
      <Grid.Column>
        <OptionsButton
          label="Theme #3"
          selected={theme === 3}
          onClick={() => onSelect({ theme: 3, color, floor })}
        />
      </Grid.Column>
      <Grid.Column>
        <OptionsButton
          label="Theme #4"
          selected={theme === 4}
          onClick={() => onSelect({ theme: 4, color, floor })}
        />
      </Grid.Column>
    </Grid.Row>
    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <OptionsHeader label="Select Your Cabinetry Color" />
      </Grid.Column>
    </Grid.Row>

    <Colors
      color={color}
      colors={colors}
      onSelect={colorId => onSelect({ theme, color: colorId, floor })}
    />

    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <OptionsHeader label="Select Your Flooring Color" />
      </Grid.Column>
    </Grid.Row>
    <Colors
      color={floor}
      colors={flooringColors}
      onSelect={colorId => onSelect({ theme, color, floor: colorId })}
    />
    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <MoreOptions />
      </Grid.Column>
    </Grid.Row>
  </Grid>
)

export default GarageOptions
